*[data-custom-select]{position:relative;display:inline-block}*[data-custom-select]>input{top:0;left:0;margin:0;padding:0;opacity:0;z-index:-1;width:100%;height:100%;position:absolute;pointer-events:none}*[data-custom-select] div.custom-select-display>*{pointer-events:none}*[data-custom-select].open div.custom-options-list{opacity:1;visibility:visible;transform:scale(1) translateZ(0);transition:opacity .3s ease,transform .3s ease,visibility 0s}*[data-custom-select]>div.custom-options-list{z-index:100;display:flex;margin-top:10px;position:absolute;flex-direction:column;background-color:#fff;opacity:0;overflow:hidden;visibility:hidden;box-sizing:border-box;transform:scale(0.95) translateZ(0);transition:visibility 0s linear .5s,opacity .3s ease,transform .3s ease}
